@media print {
  * {
    text-shadow: none !important;
    color: #000 !important;
    background: transparent !important;
    box-shadow: none !important;
  }
  @page {
    margin: 2cm 0.5cm;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  pre,
  blockquote,
  tr,
  img {
    page-break-inside: avoid;
  }
  h2,
  h3 {
    page-break-after: avoid;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  header {
    border-bottom: 1px solid #ccc;
  }
  header,
  header .container {
    height: auto;
  }
  header nav {
    display: none;
  }
  nav.subnav {
    display: none;
  }
  footer .social {
    display: none;
  }
  .btn {
    border: 1px solid #ccc;
  }
}
